export default {
  async fetch() {
    this.posts = await this.$http.$get(
      // 'https://jsonplaceholder.typicode.com/posts'
      // 'http://localhost/sites/ag_sophrologie/wp-json/wp/v2/posts'
      this.$axios.defaults.baseURL + '/posts'
    )
  },
  fetchOnServer: false,

  data() {
    return {
      posts: [],
      counter: 1,
    }
  },
}
